import React, { useState } from 'react';
import './CreaNuevoUsuario.css'
import MuestraUsuarios from './MuestraUsuarios';

const CreaNuevoUsuario = () => {
    const [usuarios, setUsuarios] = useState([
        {
          id: 1,
          name: 'Santiago',
          surname: 'Castellano Hernández',
          author: 'SantiCHP',
          email: 'miemail@midominio.com',
          password: 'superseguro*',
        },
        {
          id: 2,
          name: 'Ana',
          surname: 'Castro Sánchez',
          author: 'Annita',
          email: 'annita@midominio.com',
          password: 'supersegura*',
        },
        {
          id: 3,
          name: 'Matias',
          surname: 'Prats Justo',
          author: 'LineaDirecta',
          email: 'matias@midominio.com',
          password: 'superseguro*',
        },
        {
          id: 4,
          name: 'Andrea',
          surname: 'Nuñez Santana',
          author: 'AEA',
          email: 'andrea@midominio.com',
          password: 'supersegura*',
        },
      ]);
    
      const [nuevoName, setNuevoName] = useState(['']);
      const [nuevoSurname, setNuevoSurname] = useState(['']);
      const [nuevoAuthor, setNuevoAuthor] = useState(['']);
      const [nuevoEmail, setNuevoEmail] = useState(['']);
      const [nuevoPassword, setNuevoPassword] = useState(['']);
  
      const gestorSubmit = (e) => {
          e.preventDefault();
     
          // Crear un nuevo objeto con los datos del formulario y...
          setUsuarios([
              ...usuarios, // ...añadírselo al array que guarda los usuarios
              {
                  id: Math.random().toString(),
                  name: nuevoName,
                  surname: nuevoSurname,
                  author: nuevoAuthor,
                  email: nuevoEmail,
                  password: nuevoPassword,
              },
          ]);
          // Limpiar formulario después de enviar los datos.
          setNuevoName('');
          setNuevoSurname('');
          setNuevoAuthor('');
          setNuevoEmail('');
          setNuevoPassword('');
          console.log(usuarios);
      };
  
      const gestorCambioNombre = (event) => {
        setNuevoName(event.target.value);
      };
      const gestorCambioApellidos = (event) => {
        setNuevoSurname(event.target.value);
      };
      const gestorCambioAutor = (event) => {
        setNuevoAuthor(event.target.value);
      };
      const gestorCambioEmail = (event) => {
        setNuevoEmail(event.target.value);
      };
      const gestorCambioPassword = (event) => {
        setNuevoPassword(event.target.value);
      };

      return (
          <div className="nuevo-usuario">
              <h1>Dar de alta un usuario</h1>
              <form className="form-nuevo-usuario" onSubmit={gestorSubmit}>
                  <input
                      onChange={gestorCambioNombre}
                      className='form-text'
                      type='text'
                      name='name'
                      id='form-name'
                      placeholder='Nombre'
                      required
                      value={nuevoName}
                  />
                  <input
                      onChange={gestorCambioApellidos}
                      className='form-text'
                      type='text'
                      name='surname'
                      id='form-surname'
                      placeholder='Apellidos'
                      required
                      value={nuevoSurname}
                  />
                  <input
                      onChange={gestorCambioAutor}
                      className='form-text'
                      type='text'
                      name='author'
                      id='form-author-usr'
                      placeholder='Autor'
                      required
                      value={nuevoAuthor}
                  />
                  <input
                      onChange={gestorCambioEmail}
                      className='form-text'
                      type='email'
                      name='email'
                      id='form-email'
                      placeholder='Correo electrónico'
                      required
                      value={nuevoEmail}
                  />
                  <input
                      onChange={gestorCambioPassword}
                      className='form-text mb-1'
                      type='password'
                      name='password'
                      id='form-password'
                      placeholder='Contraseña'
                      required
                      value={nuevoPassword}
                  />
                  <button className='btn-creausuario' type='submit'>
                      REGISTRAR
                  </button>
              </form>
              <MuestraUsuarios key={usuarios.id} listaUsuarios={usuarios} />
          </div>
      )
}

export default CreaNuevoUsuario
