import React from 'react';
import './MuestraUsuarios.css'

const MuestraUsuarios = (props) => {
    let datosUsuarios = props.listaUsuarios;

    return (
        <div className="muestra-usuarios">
            <h2>Lista de usuarios/autores.</h2>
            <ul>
                {datosUsuarios.map((e) => {return <li key={e.id}>{e.author}</li>})}

            </ul>
        </div>
    )
}

export default MuestraUsuarios
